@include tablet {
    .seccion-inferior{
        display: grid;
        grid-template-columns: 2fr 1fr;
        column-gap: 2rem;
    }
}

.entrada-blog{
    margin-bottom: 2rem;

    @include tablet {
        display: grid;
        grid-template-columns: 1fr 2fr;
        column-gap: 2rem;
    }
    &:last-of-type{
        margin-bottom: 0;
    }
}

.texto-entrada{
    a{
        color: $negro;
        margin-top: 2rem;
        display: block;
        @include tablet {
            margin-top: 0px;
        }
    }
    h4{
        margin: 0;
        margin-top: 6px;
        line-height: 1.4rem;

        &::after{
            content: '';
            display: block;
            width: 15rem;
            height: .5rem;
            background-color: $verde;
            margin-top: 1rem;
        }
    }
    span{
        color: $amarillo;
    }
}